import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

const BagItem = ({
  bag: {
    disccollection,
    imageURL,
    user: { _id, name, avatar },
  },
}) => {
  const discs = disccollection.map((disc) => (
    <tr key={disc._id}>
      <td>{disc.discname}</td>
      <td>{disc.discmanufacturer}</td>
      <td>{disc.disctype}</td>
      <td>{disc.discspeed}</td>
      <td>{disc.discglide}</td>
      <td>{disc.discturn}</td>
      <td>{disc.discfade}</td>
    </tr>
  ));

  return (
    <Fragment>
      <div className='disc-table'>
        <h2 className='my-2'>{name}'s Disc Golf Bag</h2>
        <Link to={`/bag/${_id}`} className='btn btn-primary'>Bag Page</Link>
        {imageURL ? <img src={imageURL} alt="Disc Bag"/> : null}
        <table className='table'>
          <thead>
            <tr>
              <th>Disc Name</th>
              <th>Disc Manufacturer</th>
              <th>Disc Type</th>
              <th>Disc Speed</th>
              <th>Disc Glide</th>
              <th>Disc Turn</th>
              <th>Disc Fade</th>
            </tr>
          </thead>
          <tbody>{discs}</tbody>
        </table>
      </div>
    </Fragment>
  );
};

BagItem.propTypes = {
  bag: PropTypes.object.isRequired,
};

export default BagItem;
