import React, { useState, Fragment, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { editDisc } from "../../actions/bag";

const EditProfile = ({ bag: { bag, loading, disctoedit},  editDisc, }) => {

  let disc = bag.disccollection.filter(item => item._id === disctoedit)
   
  const [ formData, setFormData ] = useState({
    _id:'',
    discname: '',
    discmanufacturer: '',
    disctype: '',
    discspeed: '',
    discglide: '',
    discturn: '',
    discfade: '',
  })

  const { discname, discmanufacturer, disctype, discspeed, discglide, discturn, discfade} = formData

  useEffect(() => {

    setFormData({
      _id: disctoedit,
      discname: loading || !disc[0].discname ? '' : disc[0].discname,
      discmanufacturer: loading || !disc[0].discmanufacturer ? '' : disc[0].discmanufacturer,
      disctype: loading || !disc[0].disctype ? '' : disc[0].disctype,
      discspeed: loading || !disc[0].discspeed ? '' : disc[0].discspeed,
      discglide: loading || !disc[0].discglide ? '' : disc[0].discglide,
      discturn: loading || !disc[0].discturn ? '' : disc[0].discturn,
      discfade: loading || !disc[0].discfade ? '' : disc[0].discfade,
    });
  }, [loading, disctoedit, disc]);


  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();
    editDisc(formData);
  };

  return (
    <Fragment>
      <Fragment>
           <h1 className="large text-primary">
       Add A Disc
      </h1>
       <small>* = required field</small>
      <form className="form" onSubmit={e => onSubmit(e)}>
        <div className="form-group">
          <input type="text" placeholder="* Disc Name" name="discname" value={discname} onChange={e => onChange(e)} required />
        </div>
        <div className="form-group">
          <input type="text" placeholder="* Disc Manufacturer" name="discmanufacturer" value={discmanufacturer} onChange={e => onChange(e)}/>
        </div>
        <div className="form-group">
          <input type="text" placeholder="disctype" name="disctype" value={disctype} onChange={e => onChange(e)}/>
        </div>
        <div className="form-group">
          <input type="number" placeholder="discspeed" name="discspeed" value={discspeed} onChange={e => onChange(e)} />
        </div>
        <div className="form-group">
          <input type="number" placeholder="discglide" name="discglide" value={discglide} onChange={e => onChange(e)} />
        </div>
        <div className="form-group">
          <input type="number" placeholder="discturn" name="discturn" value={discturn} onChange={e => onChange(e)} />
        </div>
        <div className="form-group">
          <input type="number" placeholder="discfade" name="discfade" value={discfade} onChange={e => onChange(e)} />
        </div>

        
        <input type="submit" className="btn btn-primary my-1" />
        <a className="btn btn-light my-1" href="dashboard.html">Go Back</a>
      </form>
    </Fragment>
    </Fragment>
  );
};

EditProfile.propTypes = {
  bag: PropTypes.object.isRequired,
  editDisc: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  bag: state.bag
});

export default connect(mapStateToProps, { editDisc })(withRouter(EditProfile));
