import React, { useState, Fragment, useEffect } from "react";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { createProfile, getCurrentProfile } from "../../actions/profile";

const EditProfile = ({ profile: { profile, loading}, createProfile, getCurrentProfile,  history }) => {
  const [formData, setFormData] = useState({
    favoritedisc: "",
    favoritebrand: "",
    nickname: "",
  });

  const { favoritedisc, favoritebrand, nickname } = formData;

  useEffect(() => {
    getCurrentProfile();

    setFormData({
      favoritedisc: loading || !profile.favoritedisc ? '' : profile.favoritedisc,
      favoritebrand: loading || !profile.favoritebrand ? '' : profile.favoritebrand,
      nickname: loading || !profile.nickname ? '' : profile.nickname
    });
  }, [loading,getCurrentProfile,profile.favoritedisc, profile.favoritebrand, profile.nickname]);


  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();
    createProfile(formData, history, true);
  };

  return (
    <Fragment>
      <h1 className='large text-primary'>Create Your Profile</h1>
      <p className='lead'>
        <i className='fas fa-user'></i> Just some basic info about discgolf
      </p>
      {/* <small>* = required field</small> */}
      <form className='form' onSubmit={e => onSubmit(e)}>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Favorite Disc'
            name='favoritedisc'
            value={favoritedisc}
            onChange={(e) => onChange(e)}
          />
          <small className='form-text'>Do you have a favorite disc?</small>
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Favorite Brand'
            name='favoritebrand'
            value={favoritebrand}
            onChange={(e) => onChange(e)}
          />
          <small className='form-text'>
            Do you have a favorite brand or are you rockfing a mixed bag?
          </small>
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Nickname'
            name='nickname'
            value={nickname}
            onChange={(e) => onChange(e)}
          />
          <small className='form-text'>What do you go by?</small>
        </div>

        <input type='submit' className='btn btn-primary my-1' />
        <Link className='btn btn-light my-1' to='/dashboard'>
          Go Back
        </Link>
      </form>
    </Fragment>
  );
};

EditProfile.propTypes = {
  createProfile: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
  getCurrentProfile: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  profile: state.profile
});

export default connect(mapStateToProps, { createProfile, getCurrentProfile })(withRouter(EditProfile));
